import React, { useState } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import InputAdornment from '@material-ui/core/InputAdornment';
import FormControl from '@material-ui/core/FormControl';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import AccountCircle from '@material-ui/icons/AccountCircle';
import { Button, Collapse, IconButton, Typography } from '@material-ui/core';
import Alert from '@material-ui/lab/Alert';
import CloseIcon from '@material-ui/icons/Close';
import { Fragment } from 'react';
import { Redirect } from 'react-router';
const useStyles = makeStyles((theme) => ({
    // root: {
    //     width: '100%',
    //     '& > * + *': {
    //       marginTop: theme.spacing(2),
    //     },
    // },
    margin: {
        margin: theme.spacing(1),
        display: "flex",
        justifyContent: "center",
        flexDirection: "column",
        width: "50vw",
        marginLeft: "12vw",
        marginTop: "2rem"
    },
    btn: {
        display: "flex",
        justifyContent: "center",
        flexDirection: "column",
        width: "50vw",
        marginLeft: "12vw",
        marginTop: "2rem"
    },
    title: {
        flexGrow: 1,
        fontSize: "2rem",
        marginLeft: "10rem",
        [theme.breakpoints.down('sm')]: {
            fontSize: "1rem",
            marginLeft: "3rem",
        },
    },
    // alert:{
    //     flexGrow: 1,
    //     fontSize: "2rem",
    //     marginLeft: "10rem",
    //     // [theme.breakpoints.down('sm')]: {
    //     //     marginLeft:"0",
    //     //     width:"100%vw"
    //     // },
    // }

}));

const Signup = () => {
    const [name, setname] = useState("")
    const [email, setemail] = useState("")
    const [password, setpassword] = useState("")
    const [redirectTorefer, setredirectTorefer] = useState(false)
    const [open, setOpen] = React.useState(false);
    const classes = useStyles()
    const loginHandler = async () => {
        const data = await fetch('/api/signin', {
            method: "POST",
            headers: {
                Accept: "application/json",
                "content-type": "application/json",
            },
            body: JSON.stringify({
                email: email,
                password: password
            })
        })
        const response = await data.json()
        if (response.status == "success") {
            setredirectTorefer(true)
        } else {
            setOpen(true)
        }
    }
    return (
        <Fragment>
            {redirectTorefer ? <Redirect to="/Homepage" /> : ""}
            {open ? 
                    <div className={classes.root}>
                        <Collapse in={open}>
                            <div className={classes.alert}>
                            <Alert
                                action={
                                    <IconButton
                                        aria-label="close"
                                        color="inherit"
                                        size="small"
                                        onClick={() => {
                                            setOpen(false);
                                        }}
                                    >
                                        <CloseIcon fontSize="inherit" />
                                    </IconButton>
                                }
                            >
                                Invalid username or password!
                            </Alert>
                            </div>
                        </Collapse>
                       
                    </div>: ""}
            <div className={classes.margin}>
                
                <Typography variant="h5" className={classes.title}>
                    Please Login !
                </Typography>
                <TextField
                    className={classes.margin}
                    id="input-with-icon-textfield"
                    label="Email"
                    InputProps={{
                        startAdornment: (
                            <InputAdornment position="start">
                                <AccountCircle />
                            </InputAdornment>
                        ),
                    }}
                    onChange={(e) => { setemail(e.target.value) }}
                />
                <TextField
                    className={classes.margin}
                    id="input-with-icon-textfield"
                    label="Password"
                    type="password"
                    InputProps={{
                        startAdornment: (
                            <InputAdornment position="start">
                                <AccountCircle />
                            </InputAdornment>
                        ),
                    }}
                    onChange={(e) => setpassword(e.target.value)}
                />
                <Button className={classes.btn} variant="contained" color="primary" onClick={loginHandler} >Submit</Button>
            </div>
        </Fragment>

    )
}

export default Signup
