import { Button, Card, CardActionArea, CardActions, CardContent, CardMedia, makeStyles, Typography } from '@material-ui/core'
import React, { useEffect, useState } from 'react'
import { Fragment } from 'react'
import { Redirect, useParams } from 'react-router'
import { Link } from 'react-router-dom'
import img1 from '../img/img1.jpg'
const useStyles = makeStyles(theme => ({
    root: {
        maxWidth: 345,
        display:"flex",
        flexDirection:"column",
        marginLeft:"1rem"
    },
    media: {
        height: 140,
    },
    [theme.breakpoints.up('md')]: {
        deco:{
            display:"flex",
            flexDirection:"row",
            marginLeft:"1rem"
        }
    },
    
}))
const Homepage = () => {
    const [posts, setposts] = useState([])
    const [redirect, setredirect] = useState(false)
    const [id, setid] = useState("")
    const [deleteNotifier, setdeleteNotifier] = useState(false)
    const classes = useStyles()
    useEffect(() => {
        const getPosts = async () => {
            const data = await fetch('/api/post', {
                method: "GET",
                headers: {
                    Accept: "application/json",
                    "content-type": "application/json",
                },
            })
            const response = await data.json()
            // console.log(response)
            setposts(response)
        }
        getPosts()
        return () => {
            console.log('clean up work from the Homepage.js')
        }
    }, [deleteNotifier])
    const editHandler=(id)=>{
        setid(id)
        console.log(id)
        setredirect(true)
    }
    const deleteHandler=async(id)=>{
            const data = await fetch(`/api/post/${id}`, {

                method: "DELETE",
                headers: {
                    Accept: "application/json",
                    "content-type": "application/json",
                },
            })
            const response = await data.json()
            setdeleteNotifier(true)
            // console.log(response)
        }
        
    return (
        <>
        {redirect?<Redirect to={`/post/edit/${id}`}/>:""}
        <div className={classes.deco}>
            {posts.map((val,i)=> (
                <Fragment  key={i}>
                    <Card className={classes.root}>
                        <CardActionArea>
                            <CardMedia
                                className={classes.media}
                                image={`${img1}`}
                                title="Contemplative Reptile"
                            />
                            <CardContent>
                                <Typography gutterBottom variant="h5" component="h2">
                                    {val.title}
                                </Typography>
                                <Typography variant="body2" color="textSecondary" component="p">
                                    {val.body}
                                </Typography>
                                <Typography variant="body2" color="textSecondary"style={{fontStyle:"italic",fontWeight:"bold"}}>
                                    {`posted By ${val.postedBy}`}
                                </Typography>
                            </CardContent>
                        </CardActionArea>
                        
                        <CardActions>
                            <Button size="small" variant="contained" style={{backgroundColor:"#8e24aa",color:"#fff"}} onClick={()=>editHandler(val._id)}>
                                Edit
                            </Button>
                            <Button size="small"variant="contained" style={{backgroundColor:"#8e24aa",color:"#fff"}} onClick={()=>deleteHandler(val._id)}>
                                Delete
                            </Button>
                        </CardActions>
                    </Card>
                </Fragment>
            ))}
        </div>
        </>
    )
}

export default Homepage
