import React, { useState } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import InputAdornment from '@material-ui/core/InputAdornment';
import FormControl from '@material-ui/core/FormControl';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import AccountCircle from '@material-ui/icons/AccountCircle';
import { Button, Collapse, IconButton, Typography } from '@material-ui/core';
import {Redirect} from 'react-router-dom'
import { Fragment } from 'react';
import Alert from '@material-ui/lab/Alert';
import CloseIcon from '@material-ui/icons/Close';

const useStyles = makeStyles((theme) => ({
    margin: {
        margin: theme.spacing(1),
        display: "flex",
        justifyContent: "center",
        flexDirection: "column",
        width: "50vw",
        marginLeft: "12vw",
        marginTop: "2rem"
    },
    btn: {
        display: "flex",
        justifyContent: "center",
        flexDirection: "column",
        width: "50vw",
        marginLeft: "12vw",
        marginTop: "2rem"
    },
   title:{
    flexGrow: 1,
    fontSize:"2rem",
    marginLeft:"10rem",
    [theme.breakpoints.down('sm')]: {
        fontSize:"1rem",
        marginLeft:"3rem",
    },
   }

}));

const Signup = () => {
    const [open, setOpen] = useState(false)
    const [name, setname] = useState("")
    const [email, setemail] = useState("")
    const [password, setpassword] = useState("")
    const [redirectToRefer, setredirectToRefer] = useState(false)
    const [error, seterror] = useState("")
    const classes = useStyles()
    const registerHandler = async () => {
        const data = await fetch('/api/signup', {
            method: "POST",
            headers: {
                Accept: "application/json",
                "content-type": "application/json",
            },
            body:JSON.stringify({
                name:name,
                email:email,
                password:password            })
        })
        const response = await data.json()
        if(response.error){
            seterror(response.error)
        }
        if (response.status == "success") {
            setredirectToRefer(true)
        } else {
            setOpen(true)
        }
    }
    return (
        <Fragment>
            {redirectToRefer? <Redirect to="/Login"/>:""}
            {open ? <div>
                    <div className={classes.root}>
                        <Collapse in={open}>
                            <div className={classes.alert}>
                            <Alert
                                action={
                                    <IconButton
                                        aria-label="close"
                                        color="inherit"
                                        size="small"
                                        onClick={() => {
                                            setOpen(false);
                                        }}
                                    >
                                        <CloseIcon fontSize="inherit" />
                                    </IconButton>
                                }
                            >
                                {error?error:""}
                            </Alert>
                            </div>
                        </Collapse>
                       
                    </div>
                </div> : ""}
        <div className={classes.margin}>
            <div>
            <Typography variant="h5" className={classes.title}>
                Please signup !
                </Typography>
                </div>
            <TextField
                className={classes.margin}
                id="input-with-icon-textfield"
                label="Name"
                InputProps={{
                    startAdornment: (
                        <InputAdornment position="start">
                            <AccountCircle />
                        </InputAdornment>
                    ),
                }}
                onChange={(e) => setname(e.target.value)}
            />

            <TextField
                className={classes.margin}
                id="input-with-icon-textfield"
                label="Email"
                InputProps={{
                    startAdornment: (
                        <InputAdornment position="start">
                            <AccountCircle />
                        </InputAdornment>
                    ),
                }}
                onChange={(e) => setemail(e.target.value)}
            />
            <TextField
                className={classes.margin}
                id="input-with-icon-textfield"
                label="Password"
                type="password"
                InputProps={{
                    startAdornment: (
                        <InputAdornment position="start">
                            <AccountCircle />
                        </InputAdornment>
                    ),
                }}
                onChange={(e) => setpassword(e.target.value)}
            />
            <Button className={classes.btn} variant="contained" color="primary" onClick={registerHandler}>Submit</Button>

        </div>
        </Fragment>

    )
}

export default Signup
