import { Button, InputAdornment, makeStyles, TextField, Typography } from '@material-ui/core';
import React, { useEffect, useState } from 'react'
import { Fragment } from 'react';
import { Redirect, useParams } from 'react-router';
const useStyles = makeStyles((theme) => ({
    margin: {
        margin: theme.spacing(1),
        display: "flex",
        justifyContent: "center",
        flexDirection: "column",
        width: "50vw",
        marginLeft: "12vw",
        marginTop: "2rem"
    },
    btn: {
        display: "flex",
        justifyContent: "center",
        flexDirection: "column",
        width: "50vw",
        marginLeft: "12vw",
        marginTop: "2rem"
    },
    title: {
        flexGrow: 1,
        fontSize: "2rem",
        marginLeft: "10rem",
        [theme.breakpoints.down('sm')]: {
            fontSize: "1rem",
            marginLeft: "3rem",
        },
    }

}));

const Editpost = () => {
    const {postId}=useParams()
    console.log(postId)
    const [title, settitle] = useState("")
    const [body, setbody] = useState("")
    const [postedBy, setpostedBy] = useState("")
    const [redirecttoRefer, setredirecttoRefer] = useState(false)
    const classes = useStyles()
    const postSubmit = async() => {
        
        const data = await fetch(`/api/post/${postId}`, {
            method: "PUT",
            headers: {
                Accept: "application/json",
                "content-type": "application/json",
            },
            body: JSON.stringify({
                title: title?title:undefined,
                body: body?body:undefined,
                postedBy:postedBy?postedBy:""
            })
        })
        const response = await data.json()
        setredirecttoRefer(true)
        // console.log(response)
    }
    useEffect(() => {
        const defaultEditPosts = async () => {
            const data = await fetch('/api/post', {
                method: "GET",
                headers: {
                    Accept: "application/json",
                    "content-type": "application/json",
                },
            })
            const response = await data.json()
            
        }
        defaultEditPosts()
        return () => {
            // console.log('clean up work from the Homepage.js')
        }
    }, [])
    return (
        <Fragment>
            {redirecttoRefer?<Redirect to="/Homepage"/>:""}
            <div className={classes.margin}>
                <div>
                    <Typography variant="h5" className={classes.title}>
                        Edit Post
                </Typography>
                </div>
                <TextField
                    className={classes.margin}
                    id="input-with-icon-textfield"
                    label="title"
                    value={title}
                    onChange={(e) => settitle(e.target.value)}
                />

                <TextField
                    className={classes.margin}
                    id="input-with-icon-textfield"
                    label="body"
                    value={body}
                    onChange={(e) => setbody(e.target.value)}
                />
                <TextField
                    className={classes.margin}
                    id="input-with-icon-textfield"
                    label="posted by"
                   value={postedBy}
                    onChange={(e) => setpostedBy(e.target.value)}
                />
                <Button className={classes.btn} variant="contained" color="primary" onClick={postSubmit} >Submit</Button>
            </div>
        </Fragment>
    )
}

export default Editpost
